export default {
	data() {
		return {
			src: [],
			
			windowHeight: 0
		}
	},
	props:{
		list:Object
	},
	created() {
		let that = this
		uni.getSystemInfo({
			success(res) {
				that.windowHeight = (uni.getSystemInfoSync().windowHeight-res.statusBarHeight)  - 60 //获取屏幕高度
			}
		})
		
	},
	methods:{
		classify(item){
			this.$emit("classify",item)//点击分类和热门搜索传值到父组件
		}
	},
	computed:{
		
